import React from "react";
import "../css/result.css";
import {ResultDB} from "../component/ResultDB";
import goingUp from "../component/goingUp.PNG";
import goingDown from "../component/goingDown.png";
class result extends React.Component{
    state = {
        value : 0,
        array : {} ,
        selectmenu : 0,
        
    }
    selectFirst = () =>{
        this.setState(current => ({selectmenu : 0}));
    }
    selectSecond = () =>{
        this.setState(current => ({selectmenu : 1}));
    }
    selectThird = () =>{
        this.setState(current => ({selectmenu : 2}));
    }
    render(){
        const{location} = this.props;
        for(let i = 0;i< ResultDB.length;i++){
            if(location.state.mbti === ResultDB[i].id){
                this.state.value = i;
                break;
            } 
        }
        if(this.state.selectmenu === 0){
            return (
                <div>
                    <div className = "result_explain">
                        <div className = "result_title">당신의 종목은...</div>
                        <div className = "menu_bar">
                            <a className = "yellow" onClick = {this.selectFirst}>종목</a>
                            <a onClick = {this.selectSecond}>성격</a>
                            <a onClick = {this.selectThird}>궁합</a>
                        </div>
                        <div className = "sub_menu">
                            보유 자산별 손익
                        </div>
                        <div className = "result_main">
                        <h3>
                            비트코인
                        </h3>
                        <h4 className = "result_mainstockEng">
                            BTC
                        </h4>
                        <div className = "result_have_left">
                            <div className = "result_have_buffer">보유 수량 : </div>
                            <div className = "result_have_buffer">매수 금액 : </div>
                            <div className = "result_have_buffer">매수평균가 : </div>
                            <div className = "result_have_buffer">평가 금액 : </div>
                            <div className = "result_have_buffer">평가 수익률 : </div>
                            <div className = "result_have_buffer">평가 손익 : </div>
                        
                        </div>

                        <div className = "result_have_right">
                            <div className = "result_have_buffer">200BTC</div>
                            <div className = "result_have_buffer">10,403,030krw</div>
                            <div className = "result_have_buffer">2,000,000krw</div>
                            <div className = "result_have_buffer">20,000,000krw</div>
                            <div className = "result_have_buffer result_have_green">68%</div>
                            <div className = "result_have_buffer result_have_red">1,000,000krw</div>

                        </div>
                        </div>
                    </div>
                </div>
            )
        }
        else if(this.state.selectmenu === 1){
            return (
                <div>
                    <div className = "result_explain">
                        <div className = "result_title">당신의 종목은...</div>
                        <div className = "menu_bar">
                            <a onClick = {this.selectFirst}>종목</a>
                            <a className = "yellow" onClick = {this.selectSecond}>성격</a>
                            <a onClick = {this.selectThird}>궁합</a>
                        </div>
                        <div className = "sub_menu">
                            보유 성격
                        </div>
                        <ul className = "result_personal">
                            <li>성격은?</li>
                            <p className = "result_personal_font">완벽하지 못할거라면 아예 시작도 안해요! 하지만 생각한 거 실천하는데 매우 오래 걸립니다! 다만 시작은 필요하나 끝은 흐지부지...<br/>처음 보는 사람한테 말 거는데 조직 생활할 땐 낯 많이 가려요!
                                뒷자리 사수 필수! 남 한테 의지하는 것도 의지하기도 싫어서 혼자 해결 하려고 합니다.
                            </p>
                            <li>조심해줘요!</li>
                            <p className = "result_personal_font">
                                내 개인 적인 얘기 남들한테 하는 거 싫어하니까 물어보지 말아줘요.
                                외로운 건 싫어요! 남들한테 폐 끼치는 거 제일 싫어요!
                                예민 보스라 백번 잘해줘도 한번 반응 안해주면 내가 뭐 잘못했나 생각해요.
                                스트레스 받으면 지금 까지 있었던 안 좋은 일들이 생각나면서 곪아가요...
                            </p>
                            <li>이런 사람이랑 잘 맞아요!</li>
                            <p className = "result_personal_font">
                                정서적 교감이 잘 통하는 사람이 좋아요! 유머 감각이 뛰어난 사람! 사소한
                                말에도 상처받는 사람이라 말을 이쁘게 하는 사람이 좋아요!
                                나를 특별한 사람으로 만들어 주는 사람!!
                            </p>

                        </ul>
                    </div>
                </div>
            )
        }
        else if(this.state.selectmenu === 2){
            return (
                <div>
                    <div className = "result_explain">
                        <div className = "result_title">당신의 종목은...</div>
                        <div className = "menu_bar">
                            <a onClick = {this.selectFirst}>종목</a>
                            <a onClick = {this.selectSecond}>성격</a>
                            <a className = "yellow" onClick = {this.selectThird}>궁합</a>
                        </div>
                        <div className = "sub_menu">
                            보유 궁합
                        </div>
                        <div className="result_compatibility">
                            <div className="result_compatibility_left">
                                <img src = {goingUp} width = '150' height = '150'/>
                                <div>
                                    같이 넣으면 떡상하는 종목
                                </div>
                                <ul>
                                    <li>삼성전자</li>
                                    <li>테슬라</li>
                                    <li>비트캐시</li>
                                </ul>
                            </div>
                            <div className="result_compatibility_right">
                                <img src = {goingDown} width = '150' height = '150'/>    
                                <div>
                                    같이 넣으면 떡락하는 종목
                                </div>
                                <ul>
                                    <li>리플</li>
                                    <li>ETF</li>
                                    <li>구글</li>
                                </ul>
                            </div>
                        </div>
                    </div>
                </div>
            )
        }
    }
}


export default result;